<template>
  <side-menu></side-menu>
  <div class="content-wrapper">
    <div class="content-header">
      <h1></h1>
    </div>
    <div class="content">
      <button class="btn btn-primary mb10" @click="addUser">+ </button>
      <div class="nav-tabs-custom theme-panel">
        <div class="tab-content">
          <table class="table table-hover">
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
            <tr v-for="item in users">
              <td>{{ item.name }}</td>
              <td>{{ item.password }}</td>
              <td>{{ item.role }}</td>
              <td>{{ item.meta.updateAt }}</td>
              <td>
                <button class="btn btn-sm btn-primary" @click="editUser($index)"></button>
                <button class="btn btn-sm btn-default" @click="delUser(item._id, $index)"></button>
              </td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  </div>
  <!---->
  <modal :show.sync="showProModal" title="">
    <div slot="modal-body" class="modal-body">
      <form class="form-horizontal" v-ajax-submit @submit.prevent="saveUser">
        <div class="form-horizontal">
          <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <input type="text" class="form-control" v-model="user.name" required>
            </div>
          </div>
          <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <input type="text" class="form-control" v-model="user.password" required>
            </div>
          </div>
          <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <select class="form-control" v-model="user.role" required>
                <option value="0"></option>
                <option value="11"></option>
              </select>
            </div>
          </div>
          <div class="form-group">
            <div class="col-sm-10 col-sm-offset-2">
              <button class="btn btn-primary" type="submit"></button>
            </div>
          </div>
        </div>
      </form>
    </div>
    <div slot="modal-footer"></div>
  </modal>

</template>
<script>

  //
  var userOpt = {
  }

  var webupload = require('../util/webupload/webuploader.min');
  module.exports = {
    data: function() {
      return {
        showProModal: false,

        //
        users: [],  //
        user: $.extend({}, userOpt), //
        userIndex: '' //

      }
    },
    route: {
      data: function(transition) {
        this.getUser();
      }
    },
    methods: {

      //
      //
      getUser: function () {
        var self = this;
        $.ajax({
          url: domain + '/users',
          type: 'GET',
          dataType: 'json'
        })
        .done(function(res) {
          if (1 == res.status) {
            self.users = res.data;
          }
        });
      },
      addUser: function () {
        this.user = $.extend({}, userOpt);
        this.showProModal = true;
      },
      editUser: function (index) {
        this.user = this.users[index];
        this.userIndex = index;
        this.showProModal = true;
      },
      //
      saveUser: function () {
        var self = this;
        var param = JSON.parse(JSON.stringify(this.user));
        //id
        $.ajax({
          url: domain + '/user',
          type: 'POST',
          dataType: 'json',
          data: param
        })
        .done(function(res) {
          if (1 == res.status) {
            if (param._id) {
              self.users.$set(self.userIndex, param);
            } else {
              param._id = res.data._id;
              self.users.push(param);
            }
            self.showProModal = false;
          }
        });
      },
      //
      delUser: function (id, index) {
        var self = this;
        if (confirm('')) {
          if (id) {
            $.ajax({
              url: domain + '/user/' + id,
              type: 'delete'
            })
            .done(function(res) {
              if (1 == res.status) {
                self.users.splice(index, 1);
              }
            });
          }
        }
      }
    }
  }
</script>
<style>
  .palent{
    background: #fff;
    border:1px solid #ddd;
    padding: 10px;
  }
  .mb10{
    margin-bottom: 10px;
  }
</style>



